import React, { Component } from 'react'

export default class Projects extends Component {
render() { return (

<div className='projects'>

  <h1>Projects</h1>
  <p><a href={'https://github.com/alexbodin'}>Alexander Bodin on GitHub</a></p>

  <section>
    <article>
      <h2>React website</h2>
      <p>This website</p>

    </article>

    <article>
      <h2>Python Stock trading bot</h2>
      <p className={'ingress'}>A bot that interfaces with an api to get realtime and historical data
          about stocks. Then analysing the stock price and using strategies
          involving different indicators to get buy/sell signals. 
      </p>
      <p>Strategies include:</p>
      <ul>
          <li>Short-term simple moving average crossing long-term sma (Golden/Death cross)</li>
          <li>Exponential moving average crossing</li>
          <li>MACD crossing 0-line</li>
          <li>MACD derivative crossing 0 (MACD changing direction)</li>
          <li>RSI crossing below/above threshold</li>
          <li>Bollinger band expanding/contracting</li>
          <li>Price falling below threshold from local maximum</li>
      </ul>
      <p>This bot is optimized with an AI to get the best coefficients for each
          trading strategy to maximize return over a testperiod. This can be for
          example looking at which short-term and long-term sma to use for
          highest return.
      </p>
      <img src={'img/StockBotScreenshot.png'} alt={'Screenshot of Stock trading bot'} />
    </article>

    <article>
      <h2>Smart home system</h2>
      <p className={'ingress'}>A self developed smart home system based on
        raspberry pi as a server for MQTT and ESP8266 as clients with a web app
        interface to control the system from a smartphone.
      </p>

      <p></p>
      <img src={'img/SmartHome.jpg'} alt={'Smart Home components'} />
    </article>

  </section>
</div>

)}
}



